@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&family=Spartan:wght@200;300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #faf9f9;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

::selection {
  background: #ffd6ba;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.TopBarElement {
  margin-right: 3%;
  padding: 2%;
  display: flex;
  justify-content: center;
}

.title {
  font-family: "Spartan";
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  color: #555b6e;
  border-radius: 25;
}

.TopBarElement:hover {
  background-color: #e8f6f3;
  font-weight: 640;
  border-radius: 5px;
}

a.title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  color: black;
  border-bottom: 3px solid #ffc64c;
  transition: 0.4s;
}

.aboutcontainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.abouttext {
  margin-right: 10%;
  margin-top: 2%;
  width: 40%;
}

.text {
  color: #555b6e;
  font-family: "Open Sans";
  letter-spacing: 0.25px;
  font-size: 16px;
  line-height: 33px;
}

.textlink {
  color: #89b0ae;
  font-weight: 600;
  text-decoration: none;
}

.textlink:hover {
  background-color: #e8f6f3;
  border-radius: 5px;
}

.headshot {
  height: 10;
}

.topbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-left: 7%;
  margin-right: 7%;
  height: 130px;
}

.initials {
  font-family: "Spartan";
  font-weight: 400;
  font-size: 55px;
  color: #555b6e;
  text-decoration: none;
}

.tabs {
  display: flex;
  justify-content: space-between;
  width: 40%;
}

.workcontainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  align-items: center;
  scroll-behavior: smooth;
}

.landingcontainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 15%;
}

.landing-img {
  width: 1500px;
  height: 500px;
}

.name {
  font-family: "Spartan";
  font-weight: 300;
  font-size: 65px;
  position: absolute;
  top: 250px;
  /* color: #555b6e; */
}

.roles {
  font-family: "Spartan";
  font-weight: 200;
  font-size: 30px;
  position: absolute;
  top: 380px;
  /* color: #555b6e; */
}

.down-arrow {
  height: 45px;
  width: 45px;
  position: absolute;
  top: 500px;
}

.projectscontainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 85%;
  align-items: center;
  flex-wrap: wrap;
}

.project-buffer {
  margin-bottom: 50px;
  margin-left: 30px;
  margin-right: 30px;
}

.project-photo {
  position: absolute;
  width: 440px;
  height: 300px;
}

.overlay {
  top: 0;
  height: 300px;
  width: 440px;
  border-radius: 10px;
  opacity: 0;
  transition: 0.5s ease;
  background-color: #faf9f9;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.project:hover .overlay {
  opacity: 0.9;
  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); */
  text-decoration: none;
}

.project-title {
  font-family: "Spartan";
  text-decoration: none;
  font-weight: 600;
  color: #555b6e;
  font-size: 24px;
}

.project-subtitle {
  font-family: "Spartan";
  text-decoration: none;
  font-weight: 400;
  color: #555b6e;
  text-align: center;
  font-size: 16px;
}

.no-underline {
  text-decoration: none;
}

.topbar {
  display: flex;
  flex-direction: row;
}

.contactcontainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 8%;
}

.contactmessage {
  font-family: "Spartan";
  font-weight: 400;
  font-size: 35px;
  margin-bottom: 1%;
}

.email {
  font-family: "Spartan";
  font-weight: 300;
  font-size: 20px;
  margin-bottom: 3%;
}

.icons {
  display: flex;
  width: 300px;
  margin-top: 0.5%;
  justify-content: space-between;
}


.detail-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.detail-headercontainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.detail-header {
  font-family: "Spartan";
  font-weight: 500;
  margin-bottom: 1%;
}

.detail-subheader {
  font-family: "Spartan";
  font-weight: 300;
  font-size: 20px;
}

.detail-text {
  margin-top: 1%;
  width: 45%;
  margin-bottom: 3%;
}

.library-feature {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: center;
}

.library-feature-left {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.library-feature-right {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 50%;
}

.library-feature-right-text {
  display: flex;
  flex-direction: column;
  margin-left: 5%;
  margin-right: 5%;
}

.feature-txt {
  padding: 12%;
}


.foodie-img {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

